.producciones{
    width: 65%;
    margin: 0px auto 30px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.produccion__container{
    background-color: #3c3c3c;
    display: flex;
    flex-direction: column;
    width: 100%;
    border-radius: 20px;
    max-width: 400px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.251);
    transition: all 500ms;
    color: var(--color__principal__2);
}

.produccion__container:hover{
    background-color: var(--color__elementos__claro);
    color: var(--color__principal__1);
}

.titulo__producciones{
    text-align: center;
    font-size: 1.2rem;
    font-weight: 600;
    text-transform: uppercase;
    font-family: var(--fuente__principal);
    color: inherit;
    padding: 15px;
}

.img__producciones{
    width: 100%;
}

.descripcion__producciones{
    text-align: center;
    font-family: var(--fuente__principal);
    color: inherit;
    font-weight: 500;
    padding: 15px;
}

@media screen and (max-width: 1200px) {

    .producciones {
        width: 100%;
        margin: 0 15px;
        max-width: 400px;
    }

    .produccion__container {
        width: 100%;
        margin: 5px;
        max-width: 400px;
    }

}

@media screen and (max-width:680px) {

    .producciones:not(:last-child){
        margin-bottom: 25px;
    }

}